body {
  background: linear-gradient(
    90deg,
    rgb(33, 1, 241) 0%,
    rgb(97, 112, 248) 100%
  );
}

.App {
  width: 560px;
  margin: auto;
  margin-top: 30px;
  padding: 10px;
  text-align: center;
  position: relative;
  background-color: black;
  height: 40%;
  border-radius: 25px;
}
h1 {
  font-family: sans-serif;
  font-size: 2em;
  font-weight: bold;
  color: #fff;
}
.todo-input {
  /*  padding: 14px 32px 14px 16px; */
  border-radius: 4px 0 0 4px;
  border: 2px solid #5d0cff;
  outline: none;
  width: 90%;
 
  background: transparent;
  color: #fff;
  margin: auto;
  
}

textarea {
  background-color: black;
  cursor: pointer;
  outline: none;
  border: none;
  color: #e2e2e2;
  
}
textarea:focus+ .todo-button {
  background-color: rgb(218, 33, 33);
  cursor: pointer;
  outline: none;
  height: 85px;
  color: #e2e2e2;
  transition: height 0.5s ease-in-out;
}
.qty{
text-align: end;
font-size: x-small;
margin: 0;
}
.todo-input:focus{
  height: 100px;
}
.todo-button {
    height: 54px;

  padding: 10px;
  border: none;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
  outline: none;
  background: linear-gradient(
    90deg,
    rgba(93, 12, 255, 1) 0%,
    rgba(155, 0, 250, 1) 100%
  );
  color: #fff;
  text-transform: capitalize;
}

ul {
  padding: 10px 0;
  background-color: black;
  border-radius: 25px;
}
.accordion-flush {
  width: 100%;

  position: absolute;
  bottom: 5%;
}
.accordion-item {
  width: 50%;
  text-align: center;
  margin: auto;
}
.accordion-button:not(.collapsed) {
  background-color: rgb(243, 212, 74);
}
@media (min-width: 320px) and (max-width: 480px) {
  .App {
    width: 100%;
    
  }
  .todo-input {

    width: 100%;
  }
}