li {
  list-style: none;
  color: #fff;

  padding: 17px;
  border-radius: 8px;
  width: 90%;
  margin: auto;
  margin-bottom: 10px;
 
}


li:nth-child(4n + 1) {
  background: linear-gradient(
    90deg,
    rgba(93, 12, 255, 1) 0%,
    rgba(155, 0, 250, 1) 100%
  );
  
}
li:nth-child(4n + 2) {
  background: linear-gradient(
    90deg,
    rgb(255, 137, 249) 0%,
    rgb(231, 34, 139) 100%
  );
}

li:nth-child(4n + 3) {
  background: linear-gradient(
    90deg,
    rgb(52, 168, 250) 0%,
    rgb(6, 100, 223) 100%
  );
}
li:nth-child(4n + 4) {
    background: linear-gradient(
      90deg,
      rgb(224, 40, 40) 0%,
      rgb(187, 6, 223) 100%
    );
  }
.actions {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.text{
    font-size: 1.3em;
    text-align: start;
}
input[type=checkbox]{

        width: 20px;
        height: 20px;
   
}
.formUpdate textarea,.button-update {
  /*  padding: 14px 32px 14px 16px; */
  border-radius: 4px ;
  border: 2px solid #5d0cff;
  outline: none;
  width: 90%;
  background: transparent;
  color: #fff;
  margin: auto;
 
 
}
.button-update{

  background: linear-gradient(
    90deg,
    rgba(93, 12, 255, 1) 0%,
    rgba(155, 0, 250, 1) 100%
  );
  border-radius: 8px;
}
.error{
  color: red;
  font-size: 13px;
  
}

@media (min-width: 320px) and (max-width: 480px) {
  li{
    width: 100%;
  }
  .text{
    font-size: 1em;
    text-align: start;
}
}