.did {
  background: #302e2e;
  color: red;
  text-decoration: line-through;

  margin: auto;
  width: 100%;
  justify-content: start;
}
.accordion{
    width: 90%;
    margin: auto;
}
.accordion-item {
  width: 100%;
  margin: 0;
  padding: 0;
}
.accordion-button {
  color: #fff;
  width: 100%;
  margin: 0;
  padding: 0;
  background-color: black;
}

.accordion-item:first-of-type .accordion-button {
  background-color: black;
}
.delete-did {
  width: 50px;
  justify-content: end;
}
.text-did{
    text-align: start;
}
